@import url(https://fonts.googleapis.com/css2?family=Glory&display=swap);
body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
        "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
        "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Glory", sans-serif;
}

.section-full {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.container-center {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 100%;
    max-width: 1200px;
}

.section-title {
    text-align: center;
}
.section-title h2 {
    position: relative;
    margin-top: 10px;
    padding-bottom: 15px;
}
.section-title h2::after {
    position: absolute;
    content: "";
    background: linear-gradient(to right, #5ca9fb 0%, #6372ff 100%);
    height: 4px;
    width: 60px;
    bottom: 0;
    margin-left: -30px;
    left: 50%;
}
.noselect {
    -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */ /* Konqueror HTML */ /* Old versions of Firefox */ /* Internet Explorer/Edge */
    user-select: none; /* Non-prefixed version, currently
                                    supported by Chrome, Edge, Opera and Firefox */
}

.header {
    position: fixed;
    width: 100%;
    opacity: 0.9;
    z-index: 10;
}
.dummy-head {
    width: 100%;
    height: 80px;
}
.header__brand__img {
    height: 50px;
    margin: 3px 20px;
}

.header-wrapper {
    display: flex;
    background-color: #f8f9fa;
    justify-content: center;
}
.navbar {
    width: 100%;
    max-width: 1200px;
}
.navbar-brand {
    color: black;
    font-size: large;
    letter-spacing: 0.05rem;
    font-weight: 700;
}

.navbar-collapse {
    justify-content: right;
}

.nav_links a {
    color: black;
    font-size: large;
    letter-spacing: 0.05rem;
    font-weight: 700;
    /* padding: 0.5rem 1.5rem; */
    margin: 0 1rem;

    /* text-transform: uppercase; */
}

.dashboard-parent-container {
    /* height: 50vh; */
    padding: 2rem 0;
}

.dashboard-parent-container h1 {
    text-align: center;
}

.scorecard-outer-box {
    width: 70%;
    margin: 0rem auto;
    padding: 1rem;
    border: 2px solid black;
    border-radius: 5px;
    display: flex;
    justify-content: space-evenly;
}

.score-box {
    display: inline;
    text-align: center;
}

.score-title {
    font-size: 20px;
}

.score {
    font-size: 30px;
}

.index-score {
    width: 80%;
    margin: 0 auto;
    font-size: 25px;
    font-weight: 600;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
}

.rates {
    margin: 2rem 0;
    text-align: center;
}

.rates hr {
    width: 50%;
    margin: 0 auto;
}

.rates span {
    display: grid;
    grid-template-columns: 75% 10% 15%;
    text-align: left;
}

.rates span p {
    margin: auto 0;
    padding: 5px;
}

.span-rates {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin: 2rem auto;
}

.span-rates hr {
    width: 100%;
}

.chart-section {
    margin: 2rem 1rem;
    padding: 1rem;
}

.chart-section .chart {
    width: 80%;
    margin: 2rem auto;
}

@media screen and (max-width: 550px) {
    .rates span {
        display: flex;
        flex-direction: column;
        text-align: left;
        margin-left: 0.5rem;
    }

    .chart-section {
        margin: 2rem 0rem;
        padding: 1rem 0;
    }

    .chart-section .chart {
        width: 80%;
        margin: 2rem auto;
    }
}

.sub-form-title {
    width: 100%;
    padding: 10px;
    font-family: "Lato", sans-serif;
    text-transform: uppercase;
    color: #555;
    font-weight: 400;
    text-align: center;
    padding: 8px 2px;
    margin: 9px 20px 0;
}
.radio-buttons {
    display: flex;
}
.form-check {
    flex: 0.5 1;
}

.dashboard-parent-container {
    padding-top: 2rem;
}

.complaintcard-outer-box {
    padding: 1rem;
    border: 2px solid black;
    border-radius: 5px;
    display: flex;
}

.complaint-box {
    display: inline;
    text-align: center;
}

.complaint-title {
    font-size: 20px;
}

.complaint {
    font-size: 30px;
}

.search {
    width: 100%;
    padding: 60px 0px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.searchTerm {
    width: 100%;
    border: 3px solid #35404f;
    padding: 5px;
    height: 40px;
    border-radius: 5px 0 0 5px;
    outline: none;
    color: #9dbfaf;
}

.searchTerm:focus {
    color: #00b4cc;
}

.searchButton {
    padding: 4px 10px;
    height: 100%;
    border: 1px solid #35404f;
    background: #35404f;
    text-align: center;
    color: #fff;
    border-radius: 0 5px 5px 0;
    cursor: pointer;
    font-size: 20px;
}

/*Resize the wrap to see the search bar change!*/
.wrap {
    width: 50%;
}

.complaint-details {
    width: 100%;
    max-width: 800px;
    padding: 20px;
    border-radius: 2px;
}
.complaints-details__title {
    display: flex;
    padding: 10px 20px;
    justify-content: space-between;
    align-items: center;
    border-bottom: 2px solid #00b4cc;
}
.complaints-details__title__right {
    display: flex;
    flex-wrap: wrap;
}
.complaints-details__title__name {
}
.complaints-details__title__cfl {
    background-color: aliceblue;
    padding: 5px 10px;
    margin: 2px 5px;
    border-radius: 50px;
    color: white;
    background-color: green;
    cursor: pointer;
    transition: 0.2s ease all;
}
.complaints-details__title__cfl:hover {
    background-color: rgb(0, 70, 0);
}
.complaints-details__body h2,
h4 {
    width: 100%;
    text-align: center;
}
.complaint-details h2 {
    font-size: 40px;
}
.complaints-details__body {
    padding-top: 20px;
}

.section {
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
}

.scorecard-outer-box {
    width: 70%;
    margin: 3rem auto;
    padding: 1rem;
    border: 2px solid black;
    border-radius: 5px;
    display: flex;
    justify-content: space-evenly;
}

.score-box {
    display: inline;
    text-align: center;
}

.score-title {
    font-size: 20px;
}

.score {
    font-size: 30px;
}

.work {
    display: block;
    padding: 1rem;
    padding-top: 2rem;
    padding-bottom: 2rem;
}

.work h1 {
    font-size: 2.5rem;
}

.work h1 a {
    color: black;
    font-size: 2rem;
    text-decoration: none;
    border: 1px solid gray;
    border-radius: 0.5rem;
    padding: 0.5rem 1rem;
}

.work h1 a:hover {
    box-shadow: inset 0rem 0rem 0.8rem cyan;
    color: gray;
}

.cards {
    display: grid;
    grid-template-columns: auto auto auto;
    margin-top: 1.3rem;
    margin-bottom: 1.3rem;
}
p {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    max-width: 400px;
}

.card-buttons {
    padding: 0.5rem;
}

@media only screen and (max-width: 700px) {
    .scorecard-outer-box {
        width: 50%;
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
    }

    .score-box {
        margin: 0.5rem 0;
    }

    .score-title {
        font-size: 20px;
    }

    .score {
        font-size: 30px;
    }

    .cards {
        display: grid;
        grid-template-columns: auto;
    }

    .work h1 {
        font-size: 2rem;
    }

    .work-card h1 {
        font-size: 1.8rem;
    }
}

.parent_home_container {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 0;
    height: 70vh;
    max-height: 1000px;
    width: 100%;
    text-align: center;
    background: url("https://images.unsplash.com/photo-1582266255765-fa5cf1a1d501?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=870&q=80");
    background-size: cover;
    background-size: cover;
    -o-background-size: cover;
}
.overlay {
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.2);
}

#home {
    z-index: 1;
    position: absolute;
}

#home h1 {
    font-size: 4rem;
    margin-bottom: 2.5rem;
    font-weight: 900;
    color: ivory;
}

#home h2 {
    margin-top: 1rem;
    /* margin-bottom: 3rem; */
    font-size: 3rem;
    font-weight: 900;
    color: ivory;
}

.home_info{
    color: black ;
    max-width: 1200px;
    margin: 1rem auto;
}

.home_info p{
    margin: 2.5rem 3rem;
    font-size: 1.8rem;
    text-align: left;   
}


.home_info h3{
    margin: 1rem 3rem;
    margin-top: 2rem;
    font-size: 1.8rem;
    text-align: left;
}

.home_info li{
    margin: 0 5rem;
    list-style: circle;
    text-align: left;
    font-size: 1.3rem;
    margin-top: 1rem;
}

.developers{
    margin: 5rem auto;
    max-width: 1200px;
}


.title{
    margin-top: 2rem;
    text-align: center;
}

hr{
    margin: 0 3rem;
}

.dots{
    border: 0;
    border-top: 0.4rem dotted black;
    margin: 1rem 45%;
}

.about_parent_container{
    display: grid;
    grid-template-columns: auto auto;
}

.myself{
    padding: 1rem;
    text-align: center;
    margin: 2rem 1rem 0rem;
}


.myself h1{
    font-size: 2.5rem;    
}

.myself hr{
    margin-bottom: 1.5rem;
    margin-top: 1.5rem;
}

.myself p{
    font-size: 1.4rem;
    line-height: 2rem;
}

.about_member{
    display: flex;
    justify-content: center;
}



@media only screen and (max-width: 720px){


    .parent_home_container {
        height: 50vh;
    }

    #home h1{
        font-size: 2.1rem;        
        margin-top: 3rem;        
    }
    
    #home h2{
        margin: 0 1rem;
        margin-top: 1rem;
        margin-bottom: 3rem;
        font-size: 1.8rem;
    }

    .home_info p{
        margin: 2rem 2rem;
        font-size: 1.3rem;
    }
    
    
    .home_info h3{
        margin: 1rem 2rem;
        font-size: 1.3rem;
    }
    
    .home_info li{
        margin: 0 2rem;
        font-size: 1rem;
        margin-top: 0.5rem;
    }

}


@media only screen and (max-width: 560px){

    .dots{
        border-top: 0.3rem dotted black;
    }

    .about_parent_container{
        display: grid;
        grid-template-columns: auto;
    }

    .myself{
        padding: 1rem;
        margin: 1rem 1rem;
    }

    .myself h1{
        font-size: 1.5rem;    
    }

    .myself hr{
        margin-bottom: 1rem;
        margin-top: 1rem;
    }
    
    .myself p{
        font-size: 1rem;
        line-height: 1.5rem;
    }

}

@media only screen and (max-width: 380px){

    #home h1{
        font-size: 1.8rem;        
        margin-top: 1rem;    
        margin-bottom: 1;    
    }
    
    #home h2{
        
        margin-bottom: 0rem;
        font-size: 1.5rem;
    }

}
.footer {
    padding: 50px 0 20px 0;
    background-color: #35404f;
    color: #878c94;
    display: flex;
    align-items: center;
    justify-content: center;
}
.footer .title {
    text-align: left;
    color: #fff;
    font-size: 25px;
}
.footer__row {
    display: flex;
    justify-content: space-between;
}

.footer .about-div {
    text-align: left;
}

.footer .payment-div .title,
p {
    display: flex;
    justify-content: left;
}

.footer .payment {
    margin: 0px;
    padding: 0px;
    list-style-type: none;
}
.footer .payment li {
    list-style-type: none;
}
.footer .payment li a {
    text-decoration: none;
    color: #fff;
    float: left;
    font-size: 25px;
    padding: 10px 0px;
}
.footer__buy_with_meta_mask {
    width: 240px;
    padding: 10px 20px;
    background-color: white;
    border-radius: 5px;
    cursor: pointer;
    transition: all 0.2s ease;
    opacity: 0.8;
}

.footer__buy_with_meta_mask img {
    width: 100%;
}
.footer__buy_with_meta_mask:hover {
    opacity: 1;
    -webkit-transform: translateY(-5px);
            transform: translateY(-5px);
}

.payment-div {
    width: 25%;
}

.footer .text-center {
    display: flex;
    justify-content: center;
}

@media screen and (max-width: 1054px) {
    .payment-div {
        width: 50%;
    }
}
@media screen and (min-width: 1221px) {
    .footer__container {
        max-width: 1200px;
        padding: 0px 40px;
    }
}

