.header {
    position: fixed;
    width: 100%;
    opacity: 0.9;
    z-index: 10;
}
.dummy-head {
    width: 100%;
    height: 80px;
}
.header__brand__img {
    height: 50px;
    margin: 3px 20px;
}

.header-wrapper {
    display: flex;
    background-color: #f8f9fa;
    justify-content: center;
}
.navbar {
    width: 100%;
    max-width: 1200px;
}
.navbar-brand {
    color: black;
    font-size: large;
    letter-spacing: 0.05rem;
    font-weight: 700;
}

.navbar-collapse {
    justify-content: right;
}

.nav_links a {
    color: black;
    font-size: large;
    letter-spacing: 0.05rem;
    font-weight: 700;
    /* padding: 0.5rem 1.5rem; */
    margin: 0 1rem;

    /* text-transform: uppercase; */
}
